<template>
  <v-container fluid fill-height>
    <div id="particles-js" />

    <v-container fluid>
      <v-layout column align-center style="width: 50%; margin: auto;">
        <intro />
        <icon-bar />
        <additional-info />
      </v-layout>
    </v-container>
  </v-container>
</template>

<script>
import 'particles.js';
import Intro from '@/components/home/Intro.vue';
import IconBar from '@/components/home/IconBar.vue';
import AdditionalInfo from '@/components/home/AdditionalInfo.vue';

let particles = require('../assets/data/particles.json');

export default {
  props: ['dark'],

  data: function() {
    return {
      particles: {
        dark: { hex: '#ffffff', rgb: { r: 255, g: 255, b: 255 } },
        light: { hex: '#808080', rgb: { r: 128, g: 128, b: 128 } },
      },
    };
  },

  components: {
    'intro': Intro,
    'icon-bar': IconBar,
    'additional-info': AdditionalInfo,
  },

  methods: {
    initParticles() {
      window.particlesJS("particles-js", particles);
      this.updateParticleColour();
    },
    updateParticleColour() {
      let { hex, rgb } = this.dark ? this.particles.dark : this.particles.light;
      pJSDom[0].pJS.particles.array.forEach((elem) => {
        elem.color.value = hex;
        elem.color.rgb = rgb;
      });
      pJSDom[0].pJS.particles.line_linked.color = hex;
      pJSDom[0].pJS.particles.line_linked.color_rgb_line = rgb;

      pJSDom[0].pJS.fn.particlesRefresh();
    },
  },

  mounted() {
    this.initParticles();
  },

  watch: {
    dark: function(curr, prev) {
      this.updateParticleColour();
    },
  },
};
</script>

<style>
#particles-js {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.subheading.home {
   max-width: 600px;
}
</style>
