<template>
  <v-container fill-height>
    <v-layout column>
      <blog-content :post="post" />
      <buttercms-footer />
    </v-layout>
  </v-container>
</template>

<script>
import { butter } from '@/assets/js/buttercms';
import BlogContent from '@/components/blog/BlogContent.vue';
import Footer from '@/components/blog/Footer.vue';

export default {
  components: {
    'blog-content': BlogContent,
    'buttercms-footer': Footer,
  },

  data() {
    return {
      post: {},
    };
  },
  methods: {
    getPost() {
      butter.post.retrieve(this.$route.params.slug)
      .then(res => { this.post = res.data })
      .catch(err => { console.log(err) });
    },
  },
  watch: {
    $route(to, from) {
      this.getPost();
    },
  },
  created() {
    this.getPost();
  },
};
</script>
