<template>
  <v-container fill-height>
    <v-layout column>
      <v-container fill-height justify-center>
        <blog-card-container v-if="posts.length > 0"
          :posts="posts"
        />
        <v-progress-circular v-else
          indeterminate
          :size="80"
        />
      </v-container>

      <buttercms-footer />
    </v-layout>
  </v-container>
</template>

<script>
import { butter } from '@/assets/js/buttercms';
import BlogCardContainer from '@/components/blog/BlogCardContainer.vue';
import Footer from '@/components/blog/Footer.vue';

export default {
  components: {
    'blog-card-container': BlogCardContainer,
    'buttercms-footer': Footer,
  },

  data() {
    return {
      posts: [],
    };
  },

  methods: {
    fetchPosts() {
      butter.post.list({
        page: 1,
        page_size: 10,
      })
      .then(res => {
        this.posts = res.data.data;
      });
    },
  },

  created() {
    this.fetchPosts();
  },
};
</script>
