<template>
  <v-tooltip right v-model="tooltip">
    <template v-slot:activator="{ on }">
      <v-layout justify-center row class="pt-2">
        <v-img style="cursor: pointer" v-on="on"
          :src="src"
          :max-width="width"
          @click="click"
        />
      </v-layout>
    </template>
    <span>Visit ButterCMS</span>
  </v-tooltip>
</template>

<script>
export default {
  data() {
    return {
      tooltip: false,
    };
  },

  computed: {
    src() {
      let white = 'https://cdn.buttercms.com/RyJ7UhcVTCRqrCFXwgCo';
      let black = 'https://cdn.buttercms.com/PGJPyIwaQ2KnOA8UyKfH';
      return this.$vuetify._data.dark ? white : black;
    },
    width() {
      if (this.$vuetify.breakpoint.mdAndUp) return 256;
      else return 168;
    },
  },

  methods: {
    click() {
      let url = 'https://buttercms.com';
      window.open(url, '_blank');
    },
  },
};
</script>
