<template>
  <v-toolbar app class="pr-0">
    <v-toolbar-side-icon>
      <v-icon medium @click="$emit('toggle')">
        {{ dark ? 'mdi-sunglasses' : 'mdi-glasses'}}
      </v-icon>
    </v-toolbar-side-icon>

    <v-spacer />

    <v-toolbar-items v-if="text">
      <v-btn flat v-for="route in routes"
        v-if="route.toolbarName"
        :key="route.toolbarName"
        :to="route.path"
      >
        {{ route.toolbarName }}
      </v-btn>
    </v-toolbar-items>

    <v-toolbar-items v-else>
      <v-btn icon v-for="route in routes"
        v-if="route.toolbarName"
        :key="route.toolbarName"
        :to="route.path"
      >
        <v-icon>{{ route.icon }}</v-icon>
      </v-btn>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
export default {
  computed: {
    dark() {
      return this.$vuetify._data.dark;
    },
    routes() {
      return this.$router.options.routes;
    },
    text() {
      return this.$vuetify.breakpoint.smAndUp;
    },
  },
};
</script>
