<template>
  <v-card :to="link">
    <v-img :src="post.featured_image" />
    <v-card-title primary-title>
      <div>
        <h3 class="headline mb-0">{{ post.title }}</h3>
        <div>{{ post.summary }}</div>
      </div>
    </v-card-title>
  </v-card>
</template>

<script>
export default {
  props: ['post'],
  computed: {
    link() {
      return `/blog/${this.post.slug}`;
    },
  },
};
</script>
